.home {
  width: 100%;
  height: 110vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #2E1F2F; /* Deep plum */
  padding: 20px;
}

.home > article {
  width: 30%;
  display: flex;
  justify-content: center;
}

.home > article > .profile_pic {
  width: 350px;
  height: 350px;
  border-radius: 50%;
  background-image: url("../My Components/Home/Assets/photo.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0px 0px 40px #C8A2C8; /* Lavender glow */
}

.home > .intro {
  width: 43%;
  color: #F4F1F5; /* Creamy white */
  display: flex;
  flex-direction: column;
}

.home > .intro > h1 {
  font-size: 40px;
  margin: 5px 0px;
}

.home > .intro > h3 {
  color: #F4F1F5;
  font-size: 30px;
  margin: 5px 0px;
}

.home > .intro > h3 > span {
  margin-left: 10px;
  color: #C8A2C8; /* Soft lavender accent */
  font-size: 30px;
}

.home > .intro > p {
  font-size: 18px;
  padding: 20px 0px;
  color: #E9DDEB; /* Muted off-white */
}

.home > .intro > aside {
  width: 100%;
  display: flex;
  align-items: center;
  margin-top: 20px;
  justify-content: flex-start;
  gap: 20px;
}

.home > .intro > aside > button {
  background-color: #D998A0; /* Warm blush pink */
  color: #FFF;
  border: none;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  border-radius: 20px;
  width: 120px;
  transition: transform 0.2s, box-shadow 0.2s;
}

.home > .intro > aside > button:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 15px #E5B7D4; /* Soft lavender-pink glow */
}

.home > .intro > aside > button:active {
  background-color: #bf7f88;
}

.home > .intro > aside > span {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 50%;
  box-shadow: 0px 0px 10px #C8A2C8;
  color: #C8A2C8;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
}

.home > .intro > aside > span:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 15px #E5B7D4;
  color: #D49ACB;
}

.home > .intro > aside > span:active {
  color: #B573A6;
}

/* Responsive Styling */
@media screen and (max-width: 1024px) {
  .home {
    flex-direction: column;
    justify-content: center;
    text-align: center;
    height: auto;
    padding: 50px 20px;
  }

  .home > article {
    margin-top: 80px;
    width: 100%;
  }

  .home > article > .profile_pic {
    width: 250px;
    height: 250px;
  }

  .home > .intro {
    width: 100%;
    padding: 20px;
  }

  .home > .intro > h1 {
    font-size: 35px;
  }

  .home > .intro > h3 {
    font-size: 25px;
  }

  .home > .intro > h3 > span {
    font-size: 25px;
  }

  .home > .intro > p {
    font-size: 16px;
  }

  .home > .intro > aside {
    justify-content: center;
    flex-wrap: wrap;
    gap: 15px;
  }

  .home > .intro > aside > button {
    font-size: 16px;
  }
}

@media screen and (max-width: 768px) {
  .home > article {
    margin-top: 50px;
    width: 100%;
  }
}

@media screen and (max-width: 600px) {
  .home > article > .profile_pic {
    width: 200px;
    height: 200px;
  }

  .home > .intro > h1 {
    font-size: 28px;
  }

  .home > .intro > h3 {
    font-size: 22px;
  }

  .home > .intro > h3 > span {
    font-size: 22px;
  }

  .home > .intro > p {
    font-size: 14px;
  }

  .home > .intro > aside {
    flex-direction: column;
    align-items: center;
  }
}
